:root {
  --cubic: linear;
  --shadow: 0px 10px 10px 0px #0000001a;
}

.container.container__hero {
  width: 100%;
}

.nav-wrapper {
  align-items: center;
  gap: 20px;
  margin-bottom: 40px;
  display: flex;
}

.nav-img {
  width: 80px;
  margin-left: 20px;
  transform: translateY(5px);
}

.hero {
  color: #f6ef00;
  background-color: #ba89a2;
  background-repeat: no-repeat;
  height: 845px;
  margin-left: -16px;
  margin-right: -16px;
  padding-top: 130px;
  font-family: Avenir Next Cyr, sans-serif;
}

.nav {
  font-family: Avenir Next Cyr, sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 96%;
}

.nav-list {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.burger-menu {
  z-index: 100;
  pointer-events: all;
  width: 100%;
  max-width: 768px;
  height: 600px;
  padding-top: 60px;
  padding-right: 40px;
  position: absolute;
  top: 0;
  right: 0;
}

.burger-menu__wrapper {
  flex-direction: column;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  display: flex;
}

.burger-menu__open-icon, .burger-menu__close-icon {
  fill: #f6ef00;
  position: relative;
}

.burger-menu__open-icon:active, .burger-menu__close-icon:active {
  fill: #f7f6d9;
}

.burger-menu__button {
  width: 40px;
  height: 40px;
  transition: transform .1s var(--cubic);
  background: none;
  border: none;
  padding: 0;
}

.burger-menu__button:hover {
  transform: scale(1.1);
}

.burger-nav .nav-list {
  flex-direction: column;
  gap: 56px;
  width: 180px;
  display: flex;
}

.nav-link {
  color: #f6ef00;
  transition: all .1s var(--cubic);
  line-height: 96%;
}

.nav-link:hover {
  color: #f7f6d9;
}

.ivent-time {
  text-align: center;
  margin-bottom: 44px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 135%;
}

.hero-desktop-flower {
  display: none;
}

.hero-logo-icon, .hero-line, .hero-arrow {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.hero-line, .hero-arrow {
  fill: #ebe757;
}

.hero-logo-icon {
  fill: #f2cc21;
  width: 100%;
  margin-bottom: 176px;
  padding: 0 20px;
}

.hero-text {
  text-align: center;
  max-width: 270px;
  margin: 27px auto;
  font-size: 16px;
  font-weight: 600;
  line-height: 96%;
  display: block;
}

.hero-arrow-link {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@media screen and (width <= 767px) {
  .hero {
    background-position: 100% 100%;
    background-size: 400px;
  }

  .nav {
    font-size: 24px;
  }
}

@media screen and (width >= 768px) {
  .hero {
    background-position: right -250px;
    background-size: 700px;
    margin-left: -32px;
    margin-right: -32px;
  }

  .nav-link {
    font-size: 24px;
  }

  .ivent-time {
    font-size: 20px;
    font-weight: 600;
  }

  .hero-text {
    font-size: 20px;
  }
}

@media screen and (width <= 1199px) {
  .nav-wrapper, .hero-arrow-desktop {
    display: none;
  }
}

@media screen and (width >= 1200px) {
  .nav-wrapper {
    padding-top: 20px;
  }

  .header-nav-list {
    flex-direction: row;
    justify-content: flex-start;
    gap: 50px;
    width: 100%;
    padding: 0;
    display: flex;
  }

  .hero {
    background-image: none;
    margin-left: -48px;
    margin-right: -48px;
    padding-top: 0;
  }

  .burger-menu {
    display: none;
  }

  .hero-logo-icon {
    margin-bottom: 43px;
  }

  .hero-desktop-flower {
    width: 200px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    transform: translate(-14px, 20px);
  }

  .hero-line {
    stroke: #ebe757;
    stroke-width: .5px;
  }

  .hero-arrow {
    transition: all .1s var(--cubic);
    display: none;
  }

  .hero-arrow:hover {
    fill: #f7f6d9;
    transform: scale(1.2);
  }

  .hero-arrow.hero-arrow-desktop {
    display: block;
  }
}

.sonata-hero {
  padding: 80px 0 40px;
}

.sonata-hero__container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 32px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.sonata-hero__left {
  flex-direction: column;
  flex: 320px;
  gap: 24px;
  min-width: 280px;
  max-width: 400px;
  display: flex;
}

.sonata-hero__title-bold {
  color: #222;
  font-size: 2.5rem;
  font-weight: 700;
}

.sonata-hero__title-accent {
  color: #f2cc21;
  font-size: 2.5rem;
  font-weight: 700;
}

.sonata-hero__subtitle {
  color: #222;
  margin-bottom: 12px;
  font-size: 1.1rem;
}

.sonata-hero__buttons {
  gap: 16px;
  margin-top: 16px;
  display: flex;
}

.sonata-hero__btn {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  padding: 12px 32px;
  font-size: 1.1rem;
  font-weight: 600;
  transition: background .2s, color .2s;
}

.sonata-hero__btn--primary {
  color: #fff;
  background: #4f3ff0;
}

.sonata-hero__btn--secondary {
  color: #4f3ff0;
  background: #fff;
  border: 2px solid #4f3ff0;
}

.sonata-hero__btn:hover {
  opacity: .9;
}

.sonata-hero__right {
  flex-direction: column;
  flex: 2 400px;
  gap: 24px;
  min-width: 320px;
  display: flex;
}

.sonata-hero__card {
  background: #fff;
  border-radius: 18px;
  flex-wrap: wrap;
  align-items: center;
  gap: 24px;
  min-width: 320px;
  padding: 24px 32px 32px;
  display: flex;
  position: relative;
  box-shadow: 0 4px 24px #00000012;
}

.sonata-hero__card-left {
  flex-direction: column;
  align-items: center;
  gap: 12px;
  display: flex;
}

.sonata-hero__logo {
  object-fit: contain;
  width: 60px;
  height: 60px;
}

.sonata-hero__play {
  margin-top: 8px;
}

.sonata-hero__card-right ul {
  color: #4f3ff0;
  margin: 0;
  padding: 0;
  font-size: 1.1rem;
  font-weight: 600;
  list-style: inside;
}

.sonata-hero__card-title {
  color: #222;
  font-size: 1.2rem;
  font-weight: 700;
  position: absolute;
  bottom: 32px;
  left: 32px;
}

.sonata-hero__card-bar {
  background: #f2cc21;
  border-radius: 0 0 18px 18px;
  width: 100%;
  height: 8px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.sonata-hero__features {
  margin-top: 16px;
}

.sonata-hero__features-title {
  color: #222;
  margin-bottom: 8px;
  font-size: 1rem;
}

.sonata-hero__features-list {
  color: #222;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  list-style: none;
}

.sonata-hero__features-list li {
  margin-bottom: 6px;
  padding-left: 20px;
  position: relative;
}

.sonata-hero__features-list li:before {
  content: "âœ”";
  color: #f2cc21;
  position: absolute;
  top: 0;
  left: 0;
}

@media (width <= 900px) {
  .sonata-hero__container {
    flex-direction: column;
    align-items: stretch;
    gap: 32px;
  }

  .sonata-hero__right, .sonata-hero__left {
    max-width: 100%;
  }

  .sonata-hero__card {
    flex-direction: column;
    align-items: flex-start;
    padding: 24px 16px 40px;
  }

  .sonata-hero__card-title, .sonata-hero__card-bar {
    left: 16px;
    right: 16px;
  }
}

@media (width <= 600px) {
  .sonata-hero {
    padding: 80px 0 20px;
  }

  .sonata-hero__container {
    padding: 0 8px;
  }

  .sonata-hero__title-bold, .sonata-hero__title-accent {
    font-size: 1.5rem;
  }

  .sonata-hero__card {
    padding: 16px 8px 32px;
  }

  .sonata-hero__card-title {
    font-size: 1rem;
    bottom: 24px;
    left: 8px;
  }

  .sonata-hero__card-bar {
    height: 6px;
    left: 0;
    right: 0;
  }

  .sonata-hero__btn {
    padding: 10px 16px;
    font-size: 1rem;
  }
}

.aboutUs {
  color: #393939;
  padding-top: 46px;
  font-family: Avenir Next Cyr, sans-serif;
}

.title.main-title {
  text-align: center;
  margin-top: 0;
}

.title {
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
  font-family: Alice, serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}

.project-text {
  margin-bottom: 15px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 180%;
}

.title.about-you__title {
  color: #393939;
  font-family: Avenir Next Cyr, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

.about-you__list {
  color: #393939;
  flex-direction: column;
  align-self: center;
  gap: 16px;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  font-family: Avenir Next Cyr, sans-serif;
  font-size: 14px;
  font-style: italic;
  font-weight: 400;
  line-height: 150%;
  display: flex;
}

.about-you__list li {
  list-style-type: disc;
  display: list-item;
}

.person-first_info-wrapper {
  flex-direction: column-reverse;
  align-items: center;
  gap: 18px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 180%;
  display: flex;
}

.person-first__photo {
  width: 252px;
}

.person-first__questions {
  background-repeat: no-repeat;
  margin-bottom: 20px;
  padding-top: 24px;
}

.person-second {
  padding-top: 20px;
}

.person-second__info {
  color: #404041;
  margin-bottom: 18px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.person-second__photo {
  width: 336px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.person-second__photo-desktop {
  display: none;
}

.person-second__questions {
  padding-top: 24px;
}

.title__questions {
  margin-bottom: 20px;
}

.accordion-item__icon {
  transition: transform .1s var(--cubic);
}

.open {
  transform: rotate(180deg);
}

.accordion {
  width: 100%;
  margin: 20px auto;
}

.accordion-item {
  background-color: #fff6fb;
  border-bottom: 1px solid;
  border-image: linear-gradient(274deg, #fff0 15%, #dc5bde66 50%, #fff0 85%) 1;
  margin: 10px 0;
}

.accordion-item-header {
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
  padding: 15px;
  font-family: Alice;
  font-weight: bold;
  line-height: 150%;
  display: flex;
  position: relative;
}

.accordion-item-body {
  max-height: 0;
  transition: min-height .1s ease-out;
  overflow: hidden;
}

.accordion-item-body-content {
  padding: 15px;
  line-height: 150%;
}

@media screen and (width >= 768px) {
  .person-first__wrapper {
    background-image: none;
    width: 100%;
    height: auto;
  }

  .person-first_info-wrapper {
    flex-direction: row;
    width: 100%;
    height: auto;
    padding: 10px;
  }

  .person-first__photo {
    float: right;
    margin: 10px;
    display: block;
  }

  .person-second__photo {
    display: none;
  }

  .person-second__photo-desktop {
    float: left;
    width: 310px;
    margin: 10px 20px 10px 10px;
    display: block;
  }
}

@media screen and (width >= 1200px) {
  .title {
    font-size: 28px;
  }

  .title.about-you__title {
    font-size: 24px;
  }

  .about-you__list {
    max-width: 600px;
    font-size: 20px;
  }

  .person-first_info-wrapper {
    font-size: 20px;
  }

  .project-text {
    margin-bottom: 48px;
    font-size: 20px;
  }

  .accordion-item, .person-second__info {
    font-size: 20px;
  }
}

.program {
  color: #393939;
  padding: 60px 0;
  font-family: Avenir Next Cyr, sans-serif;
}

.title.title_program {
  text-align: center;
  margin-bottom: 30px;
  font-size: 36px;
  font-weight: 600;
}

.program__description {
  text-align: center;
  color: #666;
  max-width: 900px;
  margin: 0 auto 50px;
  font-size: 16px;
  line-height: 1.6;
}

.features-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  padding: 0 15px;
  display: grid;
}

.feature-card {
  box-shadow: var(--shadow);
  background: #fff;
  border-radius: 10px;
  padding: 30px;
  transition: transform .3s;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-icon {
  background-color: #f5f5f5;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  margin-bottom: 20px;
  display: flex;
}

.feature-icon .icon {
  fill: #393939;
}

.feature-title {
  color: #393939;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 600;
}

.feature-description {
  color: #666;
  font-size: 14px;
  line-height: 1.6;
}

@media screen and (width >= 768px) {
  .features-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .title.title_program {
    font-size: 42px;
  }

  .program__description {
    font-size: 18px;
  }
}

@media screen and (width >= 1200px) {
  .features-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .title.title_program {
    font-size: 48px;
  }

  .feature-title {
    font-size: 20px;
  }

  .feature-description {
    font-size: 16px;
  }
}

.service {
  background-position: right 259px;
  background-repeat: no-repeat;
  background-size: 73px;
  padding-bottom: 48px;
  font-family: Avenir Next Cyr, sans-serif;
}

.service__content {
  position: relative;
}

.service__packages.hidden {
  display: none;
}

.service-title-img {
  width: 40px;
}

.service-title-wrapper {
  justify-content: center;
  align-items: center;
  margin-bottom: 18px;
  display: flex;
}

.title.service__title {
  color: #f2cc21;
  margin: 0 14px;
  font-size: 26px;
  font-weight: 600;
}

.title.service-info__title {
  text-align: start;
  margin: 0;
}

.service__wrapper {
  text-align: start;
  color: #393939;
  flex-direction: column;
  align-items: center;
  padding: 0 30px 20px;
  display: flex;
}

.service__info {
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

.service__info-text, .service__amount {
  font-size: 14px;
  line-height: 150%;
}

.service__amount {
  text-align: end;
}

.service__reserve-amount {
  text-align: end;
  font-size: 10px;
  line-height: 95%;
}

.service__button {
  color: #f2cc21;
  text-align: center;
  cursor: pointer;
  width: 188px;
  height: 38px;
  transition: all .1s var(--cubic);
  background-color: #0000;
  border: 3px solid #f2cc21;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  font-family: Alice;
  font-size: 20px;
  line-height: 150%;
}

.service__button:hover {
  box-shadow: var(--shadow);
}

.service__button:active {
  color: #f7f6d9;
  border-color: #f7f6d9;
}

.payment-form {
  background-color: #fff;
  border-radius: 12px;
  max-width: 800px;
  margin: 0 auto;
  padding: 24px;
  font-family: Avenir Next Cyr, sans-serif;
  position: relative;
  box-shadow: 0 4px 6px #0000001a;
}

.payment-form__header {
  margin-bottom: 24px;
}

.payment-form__back-link {
  color: #393939;
  margin-bottom: 16px;
  font-size: 16px;
  text-decoration: none;
  display: inline-block;
}

.payment-form__back-link:hover {
  color: #f2cc21;
}

.payment-form__options {
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-bottom: 32px;
  display: flex;
}

.payment-form__or {
  color: #393939;
  font-size: 18px;
}

.payment-option {
  flex: 1;
  max-width: 300px;
}

.payment-option__button {
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #f2cc21;
  border-radius: 12px;
  width: 100%;
  padding: 24px;
  transition: all .2s;
}

.payment-option__button h4 {
  color: #f2cc21;
  margin-bottom: 12px;
  font-size: 20px;
}

.payment-option__button p {
  color: #393939;
  margin-bottom: 8px;
  font-size: 14px;
}

.payment-option__button .payment-option__price {
  color: #393939;
  font-size: 16px;
  font-weight: bold;
}

.payment-option__button:hover {
  background-color: #f2cc21;
}

.payment-option__button:hover h4 {
  color: #fff;
}

.payment-form__info {
  border-top: 1px solid #eee;
  margin-top: 32px;
  padding-top: 24px;
}

.payment-form__info-block {
  margin-bottom: 24px;
}

.payment-form__info-block h4 {
  color: #393939;
  margin-bottom: 8px;
  font-size: 18px;
}

.payment-form__info-block p {
  color: #666;
  font-size: 14px;
  line-height: 1.5;
}

.payment-form__info-block:last-child {
  margin-bottom: 0;
}

.payment-form__registration form {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.payment-form__registration .modal-form__input {
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
}

.payment-form__registration .modal-form__input:focus {
  border-color: #f2cc21;
  outline: none;
}

.payment-form__registration .modal-form__submit {
  color: #fff;
  cursor: pointer;
  background-color: #f2cc21;
  border: none;
  border-radius: 20px;
  padding: 12px 24px;
  font-size: 18px;
  transition: all .2s;
}

.payment-form__registration .modal-form__submit:hover {
  background-color: #f2cc21;
}

.payment-form__registration .privat-button {
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #f2cc21;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  padding: 12px;
  transition: all .2s;
  display: flex;
}

.payment-form__registration .privat-button:hover {
  background-color: #f2cc21;
}

@media screen and (width <= 768px) {
  .payment-form__options {
    flex-direction: column;
  }

  .payment-option {
    width: 100%;
  }

  .payment-form__or {
    margin: 12px 0;
  }
}

@media screen and (width >= 1200px) {
  .title.service__title {
    font-size: 26px;
  }

  .service__info {
    width: 700px;
  }

  .service__info-text, .service__amount {
    font-size: 20px;
  }

  .service__reserve-amount {
    font-size: 18px;
  }
}

.registration-steps .step {
  margin-bottom: 32px;
}

.registration-steps .step h3 {
  color: #393939;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
  font-size: 18px;
  display: flex;
}

.registration-steps .step-number {
  color: #fff;
  background-color: #393939;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  font-size: 14px;
  display: inline-flex;
}

.license-options, .license-period-individual {
  flex-direction: column;
  gap: 8px;
  margin-bottom: 24px;
  display: flex;
}

.license-option {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  align-items: center;
  padding: 16px;
  transition: all .2s;
  display: flex;
}

.license-option input[type="radio"] {
  margin-right: 12px;
}

.license-option .option-text {
  color: #393939;
  flex: 1;
  font-size: 16px;
}

.license-option .option-price {
  color: #393939;
  font-weight: bold;
}

.license-option:hover {
  border-color: #f2cc21;
}

.license-option--selected {
  background-color: #f8f9ff;
  border-color: #f2cc21;
}

.registration-form {
  border: 1px solid #ddd;
