.loading {
  z-index: 999;
  width: 2em;
  height: 2em;
  margin: auto;
  position: fixed;
  inset: 0;
  overflow: visible;
}

.loading:before {
  content: "";
  background-color: #0000004d;
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.loading:not(:required) {
  font: 0 / 0 a;
  color: #0000;
  text-shadow: none;
  background-color: #0000;
  border: 0;
}

.loading:not(:required):after {
  content: "";
  color: red;
  -ms-animation: spinner 1.5s infinite linear;
  -webkit-box-shadow: 1.5em 0 #dc5bde80, 1.1em 1.1em #dc5bde80, 0 1.5em #dc5bde80, -1.1em 1.1em #dc5bde80, -1.5em 0 #00000080, -1.1em -1.1em #00000080, 0 -1.5em #dc5bde80, 1.1em -1.1em #dc5bde80;
  border-radius: .5em;
  width: 1em;
  height: 1em;
  margin-top: -.5em;
  font-size: 10px;
  animation: 1.5s linear infinite spinner;
  display: block;
  box-shadow: 1.5em 0 #dc5bde80, 1.1em 1.1em #dc5bde80, 0 1.5em #dc5bde80, -1.1em 1.1em #dc5bde80, -1.5em 0 #dc5bde80, -1.1em -1.1em #dc5bde80, 0 -1.5em #dc5bde80, 1.1em -1.1em #dc5bde80;
}

@keyframes spinner {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

